import router from '../router'
export let wxPay = function(parameters = {}) {
  console.log(parameters)
  let payConfig = parameters
  WeixinJSBridge.invoke(
    'getBrandWCPayRequest',
    {
      appId: payConfig['appId'],
      timeStamp: payConfig['timeStamp'], // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
      nonceStr: payConfig['nonceStr'], // 支付签名随机串，不长于 32 位
      package: payConfig['package'], // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
      signType: payConfig['signType'], // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
      paySign: payConfig['paySign'] // 支付签名
    },
    function (res) {
      if (res.err_msg == 'get_brand_wcpay_request:ok') {
        alert('支付成功')
        router.push({name: 'complied', params: {code: res.err_msg, msg: '支付成功'}})
      } else if (res.err_msg == 'get_brand_wcpay_request:cancel') {
        alert('支付过程中用户取消')
        router.push({name: 'complied', params: {code: res.err_msg, msg: '支付过程中用户取消'}})
      } else if (res.err_msg == 'get_brand_wcpay_request:fail') {
        alert('支付失败')
        router.push({name: 'complied', params: {code: res.err_msg, msg: '支付失败'}})
      } else {
        alert('未知异常')
        router.push({name: 'complied', params: {code: res.err_msg, msg: '未知异常'}})
      }
    }
  );
};



