<template>
  <div>
    <div>
      <div slot='header' class="comtitle">
        <img src="../../images/success.png" alt="" srcset="" width="50px" height="50px">
        <p>{{ title }}</p>
      </div>
      <div slot='content' class="comcon">
        <p>卡券编号：{{ orderNo }}</p>
      </div>
      <div slot='footer' class="comfooter">
        <router-link to='/'>页面将在5s内自动跳转，如没有跳转请点击</router-link>
      </div>
    </div>
  </div>
</template>

<style lang="" scoped>
</style>

<script>
import { getCouponUsingGET } from '@/../client/javascript/vue-api-client.js';
import { wxPay } from '@/utils/weixin.js';
export default {
  data() {
    return {
      title: this.$route.params.msg,
      orderNo: null
    };
  },
  beforeMount: function() {
    let orderNo = { orderNo: this.$store.state.orderNo };
    if (!orderNo) {
      getCouponUsingGET(orderNo).then(res => {
        this.orderNo = res.body;
      });
    }
  }
};
</script>
<style>
.comtitle {
  font-size: 1.4em;
  padding-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.comtitle img {
  padding-right: 20px;
}
.comcon {
  margin: 40px;
}
.comcon p {
  font-size: 1.2em;
  color: grey;
}
.comfooter {
  margin: 40px;
}
.comfooter a {
  font-size: 0.8em;
}
</style>



